
import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor, isPermission } from '../../../utils/common'
import { CustomButton } from '../../globalComponents'
import moment from 'moment'
import { useCustomTranslation } from '../../../translation'
import { Icon } from '../../commanComponent'

const AttendanceLockTable = (props) => {
    const {t}=useCustomTranslation()
    const {
        allAttendanceLocks,
        isLoading,
        toggleCreateUpdateAttendanceLockModal,
        handleAttendanceLockDeleteModal,
        currentPage,
        setCurrentPage
    } = props
    const [attendanceLockList, setAttendanceLock] = useState(allAttendanceLocks)

  const updateAttLockPer = isPermission("update_attendance_lock")


    const handleUpdateAttendanceLock = (data) => {
        toggleCreateUpdateAttendanceLockModal(data)
    }

    useEffect(() => {
        setAttendanceLock(allAttendanceLocks)
    }, [allAttendanceLocks])


    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index+1}</span>
        ),
        is_lock: item => (
            <span
                className={`badge ${getBadgeColor(item.is_lock ? "Active" : "Inactive")}`}
            >
                {item.is_lock ? "Active" : "Inactive"}
            </span>
        ),
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (

            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                   {updateAttLockPer && <li>
                        <span className="dropdown-item"
                            onClick={() => {
                                handleUpdateAttendanceLock(item);
                            }}
                        >
                            <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                    </li>}
                </ul>
            </div>
    
        )
    }), [toggleCreateUpdateAttendanceLockModal]);


    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'month', label: 'Month' },
        { key: 'is_lock', label: 'Is Lock' },
        ((updateAttLockPer) &&{ key: 'action', label: 'Action' })
    ];
    return (
        <>
            <CustomTable
                data={attendanceLockList?.data}
                fields={fields}
                isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={attendanceLockList?.totalPages}
                tableName={"Attendance Lock"}
                // onSearch={handleSearch}
                scopedSlots={scopedSlots}
            />
        </>
    )
}

export default AttendanceLockTable
