import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../globalComponents'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { validateSchema } from '../../../utils/ValidationHelper'
import {  addUpdateAttendanceLockSchema } from '../../../utils/ValidationSchema'
import { useCustomTranslation } from '../../../translation'
import { useSelector } from 'react-redux'

const CreateUpdateAttendanceLock = (props) => {
    const { attendanceLockPending, updateAttendanceLockPending } = useSelector(state => state?.attendanceDashboard)
    const {t}=useCustomTranslation()
    const {
        selectedAttendanceLockData,
        openModel,
        handleOpenClose,
        handleCreateUpdateAttendanceLock
    } = props
    const [errors, setErrors] = useState({})
    const [createUpdateAttendanceLockFormData, setCreateUpdateAttendanceLockFormData] = useState({
        month: '',
        is_lock: true
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e.target ? e.target : { value: e };

        setCreateUpdateAttendanceLockFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateAttendanceLock = async () => {
        const validationResponse = await validateSchema(addUpdateAttendanceLockSchema, createUpdateAttendanceLockFormData);
        if (validationResponse.isValid) {
            const {
                month,
                is_lock
            } = createUpdateAttendanceLockFormData
            const params = {
                month,
                is_lock,
                year:"2025"
            }
            
            handleCreateUpdateAttendanceLock(params)
            setErrors({})
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(()=>{
        if(selectedAttendanceLockData){
            setCreateUpdateAttendanceLockFormData({
                month: selectedAttendanceLockData?.month,
                is_lock: selectedAttendanceLockData?.is_lock
            })
        }
    },[selectedAttendanceLockData])

    const resetForm = () => {
        setCreateUpdateAttendanceLockFormData({
            month: '',
            is_lock: true
        })
        setErrors({})
    }

    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                    {selectedAttendanceLockData ? t("headings.updateAttendanceLock"): t("headings.createAttendanceLock")} 
                    </h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>
           

                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                <Label text={t("labels.month/year")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="month"
                                        value={createUpdateAttendanceLockFormData?.month}
                                        name="month"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.month && (
                                        <CustomError text={errors.month} />
                                    )}
                                </div>
                            </div>
                        </div>
                      
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                     <Label  text={t("labels.isLock")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_lock"}
                                        id={`is_lock`}
                                        checked={createUpdateAttendanceLockFormData?.is_lock}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_lock"}
                                    />

                                </div>
                            </div>
                        </div>

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedAttendanceLockData ? t("btns.updateLock") : t("btns.addLock")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdateAttendanceLock()}
                            isLoading={selectedAttendanceLockData ? updateAttendanceLockPending : attendanceLockPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateAttendanceLock
