import React, { useEffect, useState } from 'react'
import { CustomButton } from '../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { attendanceDashboardActions, } from '../../../core/resource/actions'
import AttendanceLockTable from './AttendanceLockTable'
import { pagination } from '../../../utils/Constants.config'
import { useCustomTranslation } from '../../../translation'
import CreateUpdateAttendanceLock from './CreateUpdateAttendanceLock'

const AttendanceLock = () => {
    const dispatch = useDispatch()
    const { t } = useCustomTranslation();

    const {
        attendanceLockPending,
        attendanceLockRes,
        attendanceLockError,

        getAttendanceLockPending,
        getAttendanceLockRes,
        getAttendanceLockError,

        updateAttendanceLockPending,
        updateAttendanceLockRes,
        updateAttendanceLockError,
    } = useSelector(state => state.attendanceDashboard)


    const [isOpenCreateUpdateAttendanceLock, setIsOpenCreateUpdateAttendanceLock] = useState(false)
    const [selectedAttendanceLockData, setSelectedAttendanceLockData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [allAttendanceLocks, setAllAttendanceLocks] = useState({})

    /* Create Update AttendanceLock */
    const toggleCreateUpdateAttendanceLockModal = (data) => {
        if (!data) {
            setSelectedAttendanceLockData('')
        } else {
            setSelectedAttendanceLockData(data)
        }
        setIsOpenCreateUpdateAttendanceLock(!isOpenCreateUpdateAttendanceLock)
    }

    const handleCreateUpdateAttendanceLock = (params) => {
        if (selectedAttendanceLockData) {
            params['_id'] = selectedAttendanceLockData?._id || selectedAttendanceLockData?.id
            dispatch(attendanceDashboardActions.updateAttendanceLockRequest(params));
        }
        else {
            dispatch(attendanceDashboardActions.attendanceLockRequest(params));
        }
    }

    useEffect(() => {
        if (!attendanceLockPending) {
            if (attendanceLockError) {
                dispatch(attendanceDashboardActions.handleClearAttendanceDashboardStore("HANDLE_CLEAR_ATTENDANCE_LOCK_STORE"))
                return;
            }
            if (attendanceLockRes) {
                if (attendanceLockRes?.code === 200) {
                    dispatch(attendanceDashboardActions.handleClearAttendanceDashboardStore("HANDLE_CLEAR_ATTENDANCE_LOCK_STORE"))
                    getAllAttendanceLocks()
                    setIsOpenCreateUpdateAttendanceLock(!isOpenCreateUpdateAttendanceLock)
                }
            }
        }
    }, [
        attendanceLockRes,
        attendanceLockError,
        attendanceLockPending
    ]);

    useEffect(() => {
        if (!updateAttendanceLockPending) {
            if (updateAttendanceLockError) {
                dispatch(attendanceDashboardActions.handleClearAttendanceDashboardStore("HANDLE_CLEAR_UPDATE_ATTENDANCE_LOCK_STORE"))
                return;
            }
            if (updateAttendanceLockRes) {
                if (updateAttendanceLockRes?.code === 200) {
                    dispatch(attendanceDashboardActions.handleClearAttendanceDashboardStore("HANDLE_CLEAR_UPDATE_ATTENDANCE_LOCK_STORE"))
                    getAllAttendanceLocks()
                    setIsOpenCreateUpdateAttendanceLock(!isOpenCreateUpdateAttendanceLock)
                }
            }
        }
    }, [
        updateAttendanceLockRes,
        updateAttendanceLockError,
        updateAttendanceLockPending
    ]);



    useEffect(() => {
        getAllAttendanceLocks()
    }, [currentPage])

    /* Get All AttendanceLocks Data */
    const getAllAttendanceLocks = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(attendanceDashboardActions.getAttendanceLockRequest(params));
    }

    useEffect(() => {
        if (!getAttendanceLockPending) {
            if (getAttendanceLockError) {
                return;
            }
            if (getAttendanceLockRes) {
                if (getAttendanceLockRes?.code === 200) {
                    const getAttendanceLockData = getAttendanceLockRes?.data
                    setAllAttendanceLocks(getAttendanceLockData)

                }
            }
        }
    }, [
        getAttendanceLockRes,
        getAttendanceLockError,
        getAttendanceLockPending
    ]);


    return (
        <>
            <div className="d-flex  justify-content-end ">
                <CustomButton
                    classNames={"btn btn-primary gradientBg"}
                    handleClick={() => toggleCreateUpdateAttendanceLockModal()}
                    text={t("btns.createNewAttendanceLock")}
                    slug={"add_attendance_lock"}
                />
            </div>

            <AttendanceLockTable
                toggleCreateUpdateAttendanceLockModal={toggleCreateUpdateAttendanceLockModal}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                allAttendanceLocks={allAttendanceLocks}
                isLoading={getAttendanceLockPending}

            />

            {isOpenCreateUpdateAttendanceLock && <CreateUpdateAttendanceLock
                openModel={isOpenCreateUpdateAttendanceLock}
                handleOpenClose={toggleCreateUpdateAttendanceLockModal}
                handleCreateUpdateAttendanceLock={handleCreateUpdateAttendanceLock}
                selectedAttendanceLockData={selectedAttendanceLockData}
            />}

        </>
    )
}

export default AttendanceLock

